<section class="first-start"><div class="container"><div class="first-start-wrap"><div class="first-start__info"><h1 class="first-start__info-title" v-html="firstStart.title"></h1><h3 class="first-start__info-subtitle">{{ firstStart.subtitle }}</h3><main-btn :elText="firstStart.btnText" elClass="first-start__info-btn" @clickAction="(firstStart.animSection = false), animFirstStart(), loginToRouter()"></main-btn></div><div class="first-start__bg"><div class="first-start__bg-circle" v-for="item in 4" :key="item"></div></div></div></div></section>